.wp-block-blocks-course-text-box {
	background-color: #a3c6b2;
	padding: 2px;
	color: #151414;
	&.text-box-align-right {
		text-align: right;
	}
	&.text-box-align-center {
		text-align: center;
	}
	&.text-box-align-left {
		text-align: left;
	}
	&.has-shadow{
		box-shadow: 0 0 20px rgba(0,0,0,0.3);
		&.shadow-opacity-10 {
				box-shadow: 0 0 20px rgba(0, 0, 0, 0.1);
			}
		
			&.shadow-opacity-20 {
				box-shadow: 0 0 20px rgba(0, 0, 0, 0.2);
			}
		
			&.shadow-opacity-30 {
				box-shadow: 0 0 20px rgba(0, 0, 0, 0.3);
			}
		
			&.shadow-opacity-40 {
				box-shadow: 0 0 20px rgba(0, 0, 0, 0.4);
			}
	}
}
